body {
  overflow: hidden;
}

main {
  width: 100vw;
  height: 100vh;
  display: flex;
}

.row {
  -ms-overflow-style: none;
  scrollbar-width: none;
  background-color: var(--background-color);
  max-width: 30%;
  flex-wrap: wrap;
  padding: 0 1px;
  display: flex;
  overflow-y: auto;
}

.row::-webkit-scrollbar {
  display: none;
}

.column {
  max-width: 32%;
  flex: 32%;
  padding: 0 .5px;
}

.column img {
  vertical-align: middle;
  width: 100%;
  opacity: .2;
  margin-top: .5px;
  transition: all 1s;
}

.column img:hover {
  z-index: 2;
  opacity: 1;
  border-radius: 5%;
  transition: all .5s;
  transform: scale(1.3);
}

.focused {
  z-index: 2;
  border-radius: 5%;
  transform: scale(1.3);
  opacity: 1 !important;
}

#toggle-button {
  width: 100px;
  height: 100px;
  background: var(--base-color1);
  z-index: 5;
  position: absolute;
  left: 30%;
}

.text-container {
  background: #000;
  background: -moz-linear-gradient(90deg, #0000 0%, var(--background-color) 15%);
  background: -webkit-linear-gradient(90deg, #0000 0%, var(--background-color) 15%);
  background: linear-gradient(90deg, #0000 0%, var(--background-color) 15%);
  filter: progid:DXImageTransform. Microsoft. gradient(startColorstr= "#000000", endColorstr= "#7e777b", GradientType= 1);
  pointer-events: none;
  z-index: 3;
  border-radius: 0% 3% 3% 0%;
  margin-left: -15%;
  padding-top: 2%;
}

.text-area {
  height: 100%;
  -ms-overflow-style: none;
  scrollbar-width: none;
  margin-left: 15%;
  padding-left: 3%;
  padding-right: 3%;
  overflow-y: auto;
}

.text-area::-webkit-scrollbar {
  display: none;
}

.about-section {
  padding-top: 40px;
}

.about-heading, .about-intro {
  color: var(--spare-color);
  font-family: Ubuntu, sans-serif;
}

.about-heading {
  text-align: center;
}

.about-intro {
  margin-top: 10px;
}

.back-slide {
  background-color: var(--background-color);
  height: 100%;
  width: 80px;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  transition: all .4s;
  display: flex;
}

.back-slide:hover {
  width: 160px;
}

.about-arrow {
  color: var(--spare-color);
  opacity: .4;
  font-size: 1rem;
  transition: all .4s;
  transform: rotate(180deg);
}

.back-slide:hover .about-arrow {
  opacity: 1;
  font-size: 2rem;
}

@media screen and (max-width: 1200px) {
  .column {
    max-width: 49%;
    flex: 49%;
  }
}

@media screen and (max-width: 730px) {
  .column {
    max-width: 100%;
    flex: 100%;
  }

  .text-container {
    pointer-events: all;
  }

  .row {
    max-width: 25%;
  }

  .back-slide {
    width: 300px;
  }

  .back-slide:hover {
    width: 350px;
  }
}

